.header {
  padding-top: 55px;
  height: 120px;

  &__btn-menu {
    display: none;
  }

  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo-title {
    display: block;
    color: $beige-text;
    font-size: 32px;
    letter-spacing: 1.8px;
    line-height: normal;
  }

  &__logo-text {
    display: block;
    font-family: 'Arial', sans-serif;
    color: #ffffff;
    font-size: 13px;
    letter-spacing: 1.4px;
    line-height: normal;
    margin-top: 5px;
    margin-left: 2px;
  }

  &__nav-item {
    margin-right: 36px;

    &:last-child {
      margin-right: 0;
    }

    & a {
      font-family: 'Arial', sans-serif;
      color: #cdcdcd;
      font-size: 15px;
      letter-spacing: -0.15px;
      position: relative;

      &::after {
        content: '';
        position: absolute;
        width: 0;
        height: 3px;
        left: 0;
        bottom: -8px;
        background-color: $beige-text;
        @include trs;
      }

      &:hover {
        color: #ffffff;

        &::after {
          content: '';
          width: 100%;
        }
      }
    }

    &.active a {
      color: #ffffff;
      position: relative;
      transition: 0.2s;

      &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 3px;
        left: 0;
        bottom: -8px;
        background-color: $beige-text;
      }
    }
  }
}

@media (max-width: 768px) {
  .header {
    padding-top: 15px;
    height: 80px;

    &__logo-title {
      font-size: 24px;
    }
    &__logo-text {
      font-size: 10px;
    }

    &__btn-menu {
      display: block;
      width: 20px;
      height: 15px;
      position: relative;
      z-index: 999;

      & span {
        height: 2px;
        background-color: #ffffff;
        width: 100%;
        border-radius: 2px;
        position: absolute;
        transition: all 0.2s;

        &:nth-child(1) {
          top: 0;
        }
        &:nth-child(2) {
          top: 6px;
        }
        &:nth-child(3) {
          top: 12px;
        }
      }

      &.active {
        & span {
          &:nth-child(1) {
            top: 6px;
            transform: rotate(-45deg);
          }
          &:nth-child(2) {
            display: none;
          }
          &:nth-child(3) {
            top: 6px;
            transform: rotate(45deg);
          }
        }
      }
    }

    &__nav {
      display: none;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      background-color: #000000;

      &.active {
        display: flex;
      }

      &-list {
        padding: 40px 0;
        width: 100%;
        overflow: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
      }

      &-item {
        margin-right: 0;
        margin-bottom: 30px;

        &:last-child {
          margin-bottom: 0;
        }

        & a {
          font-size: 18px;
        }
      }
    }
  }
}
